import { useEffect, useState, createContext, useReducer } from "react";
import styles from "../css-modules/Dashboard.module.css";
import { PieCardSmall, PieCardLarge } from "../components/PieChart";
import {
  ProgressCardSmall,
  ProgressCardLarge,
} from "../components/ProgressWidget";
import AddWidget from "../components/AddWidget";
import WorthOverviewCard from "../components/WorthOverview";
import axios from "axios";
import { backend } from "../firebaseConfig";

export const EditContext = createContext();

const Dashboard = ({ data }) => {
  const [dashLength, setDashLength] = useState(0);
  const [editMode, toggleEdit] = useState(false);
  const [addMode, toggleAdd] = useState(false);
  const [dashItems, setDashItems] = useState(null);

  const [saved, toggleSaved] = useState(true);

  useEffect(() => {
    if (data.dashItems != null) setDashItems(mapDashboard(data.dashItems));
  }, []);

  useEffect(() => {}, [dashItems]);

  const mapDashboard = (items) => {
    let total = 0;

    let arr = items.map((dashItem, i) => {
      switch (dashItem) {
        case "PieCardSmall":
          total += 1;

          return (
            <PieCardSmall
              key={i}
              index={i}
              data={data}
              handleDelete={handleDelete}
            />
          );
        case "PieCardLarge":
          total += 2;
          return (
            <PieCardLarge
              key={i}
              index={i}
              data={data}
              handleDelete={handleDelete}
            />
          );

        case "ProgressCardSmall":
          total += 1;
          return (
            <ProgressCardSmall
              key={i}
              index={i}
              data={data}
              handleDelete={handleDelete}
            />
          );
        case "ProgressCardLarge":
          total += 2;
          return (
            <ProgressCardLarge
              key={i}
              index={i}
              data={data}
              handleDelete={handleDelete}
            />
          );
        case "WorthOverviewCard":
          total += 1;
          return (
            <WorthOverviewCard
              key={i}
              index={i}
              data={data.balanceSheet}
              handleDelete={handleDelete}
            />
          );
        default:
          console.log("Default");
          return;
      }
    });
    setDashLength(total);
    return arr;
  };

  function handleAdd(e) {
    e.preventDefault();

    toggleAdd(true);
  }

  function handleToggleAdd(e) {
    e.preventDefault();
    if (parseInt(e.target.getAttribute("length")) + dashLength <= 6) {
      toggleSaved(false);

      setDashItems((prevState, props) => {
        //console.log(mapDashboard([e.target.getAttribute("additem")]));
        return mapDashboard(
          prevState
            .map((item) => item.type.name)
            .concat([e.target.getAttribute("additem")])
        );
      });
    }
    console.log("Added if Possible");
    toggleAdd(!addMode);
  }

  const handleToggleCancel = (e) => {
    e.preventDefault()

    toggleAdd(!addMode);
  }

  async function handleDelete(e) {
    e.preventDefault();
    let parent = e.target.parentElement.parentElement.parentElement;
    //console.log(parent);
    if (false) {
      axios
        .post("/api/users/removeDashItem", {
          index: parent.getAttribute("id"),
          user: data.uid,
        })
        .then((res) => {
          const newData = res.data;
          console.log(newData);
          setDashItems(mapDashboard(newData));
          //console.log(dashItems);
        });
      console.log("Saved");
    } else {
      setDashItems((prevState, props) => {
        //console.log(prevState);
        prevState.splice(parent.getAttribute("id"), 1);
        return mapDashboard(prevState.map((item) => item.type.name));
      });
    }
  }

  async function handleSave(e) {
    e.preventDefault();
    toggleEdit(!editMode);
    await axios
      .post(`${backend}/api/users/saveDash`, {
        dashItems: dashItems.map((item) => item.type.name),
        user: data.uid,
        token: document.cookie.split(";")[0].split("=")[1] === undefined
        ? ""
        : document.cookie.split(";")[0].split("=")[1]
      })
      .then((res) => {
        data.dashItems = res.data;
        setDashItems(mapDashboard(res.data));
      });
    toggleSaved(true);
  }

  function handleCancel(e) {
    e.preventDefault();
    toggleEdit(!editMode);
    setDashItems(mapDashboard(data.dashItems));
  }

  function handleToggleEdit(e) {
    e.preventDefault();
    toggleEdit(!editMode);
  }

  if (data) {
    return (
      <div id={styles.dash}>
        <div id={styles.container}>
          {addMode && <AddWidget toggleAdd={handleToggleAdd} handleToggleCancel={handleToggleCancel}/>}
          <EditContext.Provider value={editMode}>
            <div className={styles.header}>
              <h2>Dashboard</h2>
              {!editMode && <button onClick={handleToggleEdit}>EDIT</button>}
              {editMode && (
                <div>
                  {true && <button onClick={handleAdd}>ADD</button>}
                  <button onClick={handleSave}>SAVE</button>
                  <button onClick={handleCancel}>CANCEL</button>
                </div>
              )}
            </div>

            <div className={styles.content}>{dashItems}</div>
          </EditContext.Provider>
        </div>
      </div>
    );
  } else {
    return <div>Dashboard</div>;
  }
};

export default Dashboard;
