const ProgressBar = (props) => {
  const { bgcolor, completed, name } = props;

  const containerStyles = {
    height: "7.5%",
    paddingLeft: '2.5%',
    paddingRight: '2.5%',
    borderRadius: 50,
    margin: "2.5%",
  };

  const fillerStyles = {
    height: "100%",
    width: `${completed}%`,
    backgroundColor: bgcolor,
    borderRadius: "inherit",
    textAlign: "right",
  };

  const labelStyles = {
    padding: 5,
    color: "white",
    fontWeight: "bold",
  };

  const nameStyles = {
    textAlign: 'left',
    marginLeft: 10
  }

  return (
    <div style={containerStyles}>
        <div style={nameStyles}>{name}</div>
      <div style={fillerStyles}>
        <span style={labelStyles}>{completed>10 && `${completed}%`}</span>
      </div>
    </div>
  );
};

export default ProgressBar;
