import styles from "../css-modules/Menu.module.css";
import { Link } from "react-router-dom";
import { signOut } from "firebase/auth";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { backend } from "../firebaseConfig";

const Menu = ({ isAuth, setAuth, setUser, auth, data, setData, accounts }) => {
  let navigate = useNavigate();

  const signUserOut = async (e) => {
    e.preventDefault();
    signOut(auth);
    console.log(data)
    const authToken = document.cookie.split(";")[0].split("=")[1] === undefined
    ? ""
    : document.cookie.split(";")[0].split("=")[1]
    await axios.post(`${backend}/api/UserAuth/signOut`, {
      token: authToken
    });
    localStorage.clear();
    document.cookie = "token=";
    setAuth(false);
    setUser(null);
    setData(null);
    navigate("/finance-app/");
  };

  return (
    <div id={styles.menu}>
      {data ? (
        <div id={styles.profile}>
          
            <img src={data.photoURL}></img>
          
          {/* <div id={styles.info}>
            <h4>{data.displayName}</h4>
            <p>{`$${
              accounts.ltAssets +
              accounts.stAssets -
              accounts.ltLiab -
              accounts.stLiab
            }`}</p>
          </div> */}
        </div>
      ) : (
        <div id={styles.profile}></div>
      )}
      <nav>
        <ul>
          <li>
            <Link to="/finance-app/">Dashboard</Link>
          </li>
          <li>
            <Link to="/finance-app/Cards">Cards</Link>
          </li>
          <li>
            <Link to="/finance-app/Budget">Budget</Link>
          </li>
          <li>
            <Link to="/finance-app/Finances">Finances</Link>
          </li>
          <li>
            <Link to="/finance-app/Worth">Worth</Link>
          </li>
          {isAuth && (
            <li>
              <button onClick={signUserOut}>Logout</button>
            </li>
          )}
          {!isAuth && (
            <li>
              <Link to="/finance-app/Login">Login</Link>
            </li>
          )}
        </ul>
      </nav>
    </div>
  );
};

export default Menu;
