import styles from "../css-modules/Main.module.css";
import Menu from "./Menu";
import Dashboard from "../pages/Dashboard";
import Explore from "../pages/Explore";

import Login from "./Login";

import Cards from "../pages/Cards";
import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  BrowserRouter as Router,
  Routes,
  Route
} from "react-router-dom";
import { auth, backend } from "../firebaseConfig.js";

const Main = () => {
  const [isAuth, setAuth] = useState(false);
  const [user, setUser] = useState("");
  const [data, setData] = useState(null);
  const [isLoaded, toggleLoaded] = useState(false);
  
  const [accounts, updateAccounts] = useState({
    stAssets: 0,
    ltAssets: 0,
    stLiab: 0,
    ltLiab: 0,
  });

  useEffect(() => {
    let token =
      document.cookie.split(";")[0].split("=")[1] === undefined
        ? ""
        : document.cookie.split(";")[0].split("=")[1];
    console.log(token);
    axios
      .post(`${backend}/api/UserAuth/userStillAuth`, {
        token: token,
      })
      .then((res) => {
        console.log(res);
        if (res.data !== "") {
          setData(res.data);
          setAuth(true);
          updateAccounts({
            ltAssets: res.data.balanceSheet.ltAssets,
            stAssets: res.data.balanceSheet.stAssets,
            ltLiab: res.data.balanceSheet.ltLiab,
            stLiab: res.data.balanceSheet.stLiab,
          });
        } else {
          setAuth(false);
        }
        console.log(isAuth);
        toggleLoaded(true);
      })
      .catch((err) => {
         setAuth(false);
         localStorage.clear();
         document.cookie = `token=`
         toggleLoaded(true)
         //window.location = 'http://192.168.50.202:3000/finance-app'
        
      });
  },[]);

  if (isLoaded) {
    return (
      <Router>
        <div id={styles.main}>
          <Menu
            isAuth={isAuth}
            setAuth={setAuth}
            setUser={setUser}
            auth={auth}
            data={data}
            setData={setData}
            accounts={accounts}
          />
          <div className={styles.content}>
            <Routes>
              <Route
                exact
                path="/finance-app/"
                element={isAuth ? <Dashboard data={data} /> : <Explore />}
              />

              <Route
                path="/finance-app/Cards"
                element={<Cards data={data} isAuth={isAuth}/>}
              />
              <Route
                path="/finance-app/Budget"
                element={<Explore currUser={user} />}
              />
              <Route
                path="/finance-app/Finances"
                element={<Explore currUser={user} />}
              />
              <Route
                path="/finance-app/Worth"
                element={<Explore currUser={user} />}
              />
              {!isAuth && (
                <Route
                  path="/finance-app/Login"
                  element={
                    <Login
                      setAuth={setAuth}
                      setUser={setUser}
                      auth={auth}
                      setData={setData}
                      updateAccounts={updateAccounts}
                    />
                  }
                />
              )}
            </Routes>
          </div>
        </div>
      </Router>
    );
  } else {
    return <div>Loading</div>;
  }
};
export default Main;
